.Testimonial{
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
    cursor: pointer;
}
.left-t{
    display: flex;
    flex: 1 1;
    gap: 2rem;
    flex-direction: column;
    text-transform: uppercase;
    color: white;
}

.left-t>:nth-child(1){
    color: var(--lightgreen);
    font-weight: bold;
}
.left-t>:nth-child(2),
.left-t>:nth-child(3){
    font-weight: bold;
    font-size: 3rem;
}


.left-t>:nth-child(4){
    text-transform: none;
    text-align: justify;
    letter-spacing: 1px;
    line-height: 40px;
    font-weight: bold; 
}

.right-t{
    flex: 1;
    position: relative;
}


/* profile zoomin  */
.right-t>img{
    position: absolute;
    width: 17rem;
    height: 20rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
    bottom: 2rem;
    transition: transform 0.5s ease-in-out;
}

.right-t>img:hover {
    transform: scale(1.02);
}



.right-t>:nth-child(1){
    position: absolute;
    width: 17rem;height: 20rem;
    border: 2px solid lightgreen;
    background-color: transparent;
    right: 9rem;
    top: 0.9rem;
}
.right-t>:nth-child(2){
    position: absolute;
    width: 17rem;height: 19rem;
    top: 4rem;
    right: 7rem;
    background: var(--lightgreen);
}

.arrows{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 16rem;
}

.arrows>img{
    width: 1.5rem;
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .Testimonial{
        flex-direction: column;
    }
    .left-t>:nth-child(2),
    .left-t>:nth-child(3){
        font-size: xx-large;
    }
    .right-t{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
    }
    .right-t>div{
        position: relative;
        display: none;
    }
    .right-t>img{
        top: 0;right: 0;position: relative;
    }
    .right-t>:last-child{
        display: block;
        bottom: 0;left: 0;
    }

} 