.Programs{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0 2rem;
}
.programs-header{
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
}

.programs-category{
    display: flex;
    gap: 1rem;
}

/* card section */
.category{
    display: flex;
    flex-direction: column;
    background-color: rgb(90, 90, 90);
    padding: 2rem;
    gap: 1rem;
    color: white;
    justify-content: space-between;
}

/* svg */
.category>:nth-child(1){
    width: 2rem;
    height: 2rem;fill: white;
}

/* pera */
.category>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}

.category>:nth-child(3){
    font-size: 0.9rem;
    line-height: 25px;
}


/* join now text */
.join-now{
    display: flex;
    align-items: center;
    gap: 2rem;
}
.join-now>img{
    width: 1rem;
}

.category:hover{
    background: var(--planCard);
    color: rgb(0, 0, 0);
    cursor: pointer;
}
@media screen and (max-width: 768px) {
    .programs-header{
        flex-direction: column;
        gap: 1rem;
        font-size: x-large;
        align-items: center;
        justify-content: center;
        margin-top: 2rem;
    }
    .programs-category{
        flex-direction: column;
    }

}